// titles
.h0-title {
  font-size: 64px;
  font-weight: 400;
  line-height: 60px; }

.h1-title {
  font-size: 44px;
  font-weight: 400;
  line-height: 1; }

.h2-title {
  font-size: 34px;
  font-weight: 400;
  line-height: 38px; }

.h3-title {
  font-size: 28px;
  font-weight: 700; }

// paragraphs
.p-huge {
  font-size: 34px;
  font-weight: 400;
  line-height: 38px; }

.p-bold {
  font-weight: bold;
  font-size: 24px;
  line-height: 28px; }

.p-main {
  font-weight: 400;
  font-size: 24px;
  line-height: 28px; }

.p-regular {
  font-size: 18px;
  font-weight: 400;
  line-height: 24px; }

.p-label {
  font-size: 14px;
  font-weight: 700;
  line-height: 34px; }

p {
  margin: 0;
  + p {
    margin-top: 1em; } }



// little inline helpers
.f-400 {
  font-weight: normal; }

.f-500 {
  font-weight: 500; }

.f-600 {
  font-weight: 600; }

.f-700 {
  font-weight: 700; }

.f-900 {
  font-weight: 900; }

// align
.tac {
  text-align: center; }

.tal {
  text-align: left; }

// color modifiers
.c-red {
  color: $colorRed; }

.c-primary {
  color: $colorPrimary; }

.c-violet {
  color: $colorViolet; }
