  content: ' ';
}

.ico {
  fill: currentColor;
}

/* UI */
.ui-group {
  position: relative;
  margin-bottom: 20px;
}

.ui-group label {
  display: block;
  margin-bottom: 10px;
}

.ui-input {
  -webkit-appearance: none;
  display: block;
  width: 100%;
  background: white;
  font-family: -apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica", "Ubuntu", "Cantarell", "Arial", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: rgba(51, 51, 51, 0.5);
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  padding: 12px 15px;
}

.ui-input.has-error {
  color: #f45b63;
}

.ui-input.has-error::placeholder {
  color: rgba(244, 91, 99, 0.5);
}

.ui-input textarea {
  resize: vertical;
}

/* Checkbox */
.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #4ab68f;
}

.ui-checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 20px;
  background-color: white;
  border: 1px solid #AFB7C0;
  border-radius: 4px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 10px;
  background: #4ab68f;
  border-radius: 50%;
  transition: transform .2s ease;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 14px;
  cursor: pointer;
}

.mfp-wrap {
