}

html.mfp-helper body {
  overflow: hidden;
  height: 100%;
  -webkit-transform: translateZ(0);
}

#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative;
}

.popup-buble .popup-dialog {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
}

.popup-buble.mfp-ready .popup-dialog {
  opacity: 1;
  transform: scale(1);
}

.popup-buble.mfp-removing .popup-dialog {
  transform: scale(0.8);
  opacity: 0;
}

.popup-buble.mfp-bg {
  background: #4ab68f;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.popup-buble.mfp-ready.mfp-bg {
  opacity: 1;
}

.popup-buble.mfp-removing.mfp-bg {
  opacity: 0;
}

.my-mfp-slide-bottom .zoom-anim-dialog {
  opacity: 0;
  transition: all 0.2s ease-out;
  transform: translateY(-20px) perspective(600px) rotateX(10deg);
}

.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
  opacity: 1;
  transform: translateY(0) perspective(600px) rotateX(0);
}

.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
  opacity: 0;
  transform: translateY(-10px) perspective(600px) rotateX(10deg);
}

.my-mfp-slide-bottom.mfp-bg {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

.my-mfp-slide-bottom.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.my-mfp-slide-bottom.mfp-removing.mfp-bg {
  opacity: 0;
}

.scaler {
  background-color: #EFEEEE;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  font-size: 0;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.scaler[data-ar='16:9'] {
  padding-bottom: 56.25%;
}

.scaler[data-ar='1:1'] {
  padding-bottom: 100%;
}

.scaler[data-ar='4:3'] {
  padding-bottom: 75%;
}

.scaler[data-ar='21:9'] {
  padding-bottom: 42.85%;
}

.scaler img {
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.scaler [js-lazy] {
  backface-visibility: hidden;
  transform-style: preserve-3d;
  will-change: opacity;
}

.scaler.is-bg-hidden {
  background-color: transparent;
}

.scaler.is-bg-hidden.no-bg-onload {
  background-image: none !important;
}

.scaler.is-loaded img,
.scaler.is-loaded picture {
  opacity: 1;
}

[js-lazy] {
  opacity: 0;
  transition: opacity 0.25s ease-in-out, transform 0.4s ease-in-out;
}

[js-lazy].is-loaded {
  opacity: 1;
}

.reveal {
  overflow: hidden;
  position: relative;
}

.reveal::after {
  background: #EFEEEE;
  content: ' ';
  display: inline-block;
  pointer-events: none;
  position: absolute 0;
  transition: transform 0.8s cubic-bezier(0.9, 0, 0.1, 1);
  z-index: 2;
}

.reveal.is-animated::after {
  transform: translate(-100%, 0);
}

.reveal.is-animated[data-direction='ltr']::after {
  transform: translate(100%, 0);
}

.reveal.is-animated[data-direction='rtl']::after {
  transform: translate(-100%, 0);
}

