  flex: 1 1 auto;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center bottom;
}

.hero__wrapper {
  position: relative;
  z-index: 2;
  height: 670px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}

.hero__text {
  max-width: 480px;
}

.hero__text.mw-700 {
  min-width: 770px;
}

.hero--gradient {
  background: #C85E5E;
}

.hero--gradient .hero__background {
  background: linear-gradient(90deg, rgba(111, 0, 147, 0.5) 0%, rgba(0, 0, 0, 0.0001) 70%);
}

.map {
  position: relative;
  font-size: 0;
}

.map img, .map picture {
  width: 100%;
}

.map-baloon {
  position: absolute;
  top: 0;
  right: 80px;
  max-width: 220px;
  background: #FFFFFF;
  box-shadow: 3px 4px 4px 3px rgba(138, 133, 133, 0.5);
}

.map-baloon__image {
  position: relative;
  font-size: 0;
}

.map-baloon__image::after {
  display: inline-block;
  content: ' ';
  position: absolute 0;
  opacity: 0.5;
  background: #E63987;
}

@supports (mix-blend-mode: multiply) {
  .map-baloon__image::after {
    opacity: 0.65;
    mix-blend-mode: multiply;
  }
}

.map-baloon__image img {
  width: 100%;
}

.map-baloon__content {
  padding: 10px 10px 10px 16px;
}

.map-baloon__name {
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.map-baloon__cta {
  margin-top: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 34px;
}

.numbers__title {
  padding: 12px 0;
  font-size: 18px;
  line-height: 24px;
  border-bottom: 1px solid currentColor;
}

.numbers__num {
  margin-bottom: 12px;
  font-size: 175px;
  font-weight: 300;
  line-height: 150px;
  letter-spacing: -0.05714em;
  color: #040858;
}

.numbers__num span {
  font-size: 133px;
  font-weight: 300;
  line-height: 150px;
  letter-spacing: 0;
}

.team-b {
  background: #EFEEEE;
}

.team-b__content {
  padding-top: 60px;
}

.team-b__img {
  margin-right: -32px;
}

.team-b__img img {
  width: 100%;
}

.team-b__cta {
  display: inline-block;
  margin-top: 14px;
  font-size: 14px;
  font-weight: 700;
  line-height: 34px;
  transition: color 0.25s ease-in-out;
}

.team-b__cta:hover {
  color: #040858;
}

.team__crumb-position {
  padding: 12px 0;
  font-size: 18px;
  line-height: 24px;
  color: #4500CF;
}

.team__crumb-bg {
  font-size: 0;
}

.team__crumb-bg img {
  width: 100%;
  height: 5px;
}

.team-card {
  display: block;
  background: #EFEEEE;
  transition: 0.25s ease-in-out;
}

.team-card:not(.team-card--big):hover, .team-card:not(.team-card--small):hover {
  box-shadow: 3px 4px 4px 3px rgba(138, 133, 133, 0.5);
}

.team-card--big {
  max-width: 450px;
}

.team-card--small {
  max-width: 210px;
}

.team-card--small .team-card__content {
  padding-left: 15px;
}

.team-card--big, .team-card--small {
  background: white;
  margin-left: auto;
  box-shadow: 3px 4px 4px 3px rgba(138, 133, 133, 0.5);
}

.team-card:hover {
  color: #333;
}

.team-card:hover .team-card__more {
  color: #333;
}

.team-card__image {
  font-size: 0;
}

.team-card__image img {
  width: 100%;
}

.team-card__content {
  padding: 20px 20px 20px 32px;
}

.team-card__links {
  margin-top: 14px;
  font-size: 14px;
  font-weight: 700;
  line-height: 34px;
  display: flex;
  flex-wrap: wrap;
}

.team-card__links a {
  position: relative;
  display: inline-block;
  transition: color 0.25s ease-in-out;
}

.team-card__links a:hover {
  color: #040858;
}

.team-card__links a::after {
  display: inline-block;
  content: '|';
  margin-left: 8px;
  margin-right: 8px;
  color: #333;
}

.team-card__links a:last-child::after {
  display: none;
}

.team-card__more {
  font-size: 14px;
  font-weight: 700;
  line-height: 34px;
  color: #9B9B9B;
  transition: color 0.25s ease-in-out;
}

.testimonials {
  background: #4A4A4A;
  color: white;
  padding: 80px 0 70px;
}

.testimonials.bg-light {
  background: #EFEEEE;
  color: #333;
}

.testimonials__image {
  max-width: 100%;
  font-size: 0;
  text-align: center;
}

.testimonials__content {
  max-width: 505px;
}

.text__tabs {
  margin: 0;
  padding: 0;
}

.text__tabs a {
  display: block;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  padding: 12px 0;
  border-bottom: 1px solid currentColor;
}

.text__tabs a.is-active {
  font-weight: 700;
}

.text__image {
  font-size: 0;
}

.text__image img {
  max-width: 100%;
}

.tab {
  display: none;
}
